import State from '@/store/State.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Reference_table
*/
class Reference_table
{
	/**
	 * auto_model_table
	*/
	auto_model_table(ematch)
	{
		let requestParams = {
			action_name: 'reference_table_get',
			method: 'GET',
			url: State.endpoint_url() + 'online/reference_table_get/?table_token=c_test_auto_model&ematch=' + ematch,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * psc_table
	*/
	psc_table(filter)
	{
		let requestParams = {
			action_name: 'reference_table_get',
			method: 'GET',
			url: State.endpoint_url() + 'online/reference_table_get/?table_token=c_test_psc&filter=' + filter,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Reference_table;
