<template>
	<select
		:name="field_id"
		:id="field_id"
		v-model="value"
		class="form-control"
	>
		<option
			v-for="(item, key) in field_data.enum"
			:value="option_value(item)"
		>
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: ['recalculate_product'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			option_value(item)
			{
				for (let key in item) {
					if (! item.hasOwnProperty(key)) { continue; }
					// we have two types of enum options, one straight with key=>value, other also with useless category data key=>object
					if (typeof item[key] === 'string') {
						return key;
					} else if (typeof item[key] === 'object') {
						return item[key].value;
					}
				}
			},
			option_label(item)
			{
				for (let key in item) {
					if (! item.hasOwnProperty(key)) { continue; }
					// we have two types of enum options, one straight with key=>value, other also with useless category data key=>object
					if (typeof item[key] === 'string') {
						return item[key];
					} else if (typeof item[key] === 'object') {
						return item[key].label;
					}
				}
			},
		},
		computed: {
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, '');
				},
				set(event_value) {
					State.save_user_input(this.field_id, event_value);
					this.$emit('recalculate_product');
				},
			},
		},
		watch: {
		},
	}
</script>
