<template>
	<div class="form-check form-check-inline input-button-icon">
		<input :class="icon_class" type="checkbox" :id="field_id" :name="field_id" v-model="value" true-value="1" false-value="0">
		<label :for="field_id">
			{{ checkbox_label }}
		</label>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
		},
		computed: {
			checkbox_label()
			{
				return this.field_data.label;
			},
			icon_class()
			{
				return this.field_data.wrapper_attrs;
			},
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, 0);
				},
				set(event_value) {
					State.save_user_input(this.field_id, event_value);
				},
			},
		},
	}
</script>
