<template>
	<div class="flatpickr_scope input-datepicker">
		<input ref="input" type="text" :id="field_id" :name="field_id" v-model="value">
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';
	import flatpickr from 'flatpickr';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
		},
		computed: {
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, '');
				},
				set() {
					let event_value = this.$refs.input.value; // flatpickr does not trigger value change
					State.save_user_input(this.field_id, event_value);
				},
			},
		},
		mounted() {
			// this.field_data.type_settings.calendar_type === 'standard' | 'years_first' - not needed anymore
			flatpickr(
				this.$refs.input,
				{
					static: true,
					dateFormat: 'd.m.Y',
					defaultDate: this.value,
					allowInput: true,
				}
			);
		},
	}
</script>

<style scoped lang="scss">
	.flatpickr_scope::v-deep() {
		@import '@/assets/libraries/flatpickr_4.6.9/flatpickr.scss';
	}
</style>
