<template>
	<div class="form-check">
		<input
			:id="field_id"
			:name="field_id"
			v-model="value"
			true-value="1"
			false-value="0"
			type="checkbox"
			class="form-check-input"
		>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: ['validate_global_changed'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, 0);
				},
				set(event_value) {
					State.save_user_input(this.field_id, event_value);
					this.$emit('validate_global_changed');
				},
			},
		},
		watch: {
		},
	}
</script>
