<template>
	<select v-model="value" class="form-control" :id="field_id" :name="field_id" :disabled="disabled">
		<option value="" disabled="disabled">--- vyber ---</option>
		<option v-for="(item, key) in options_table" :value="option_value(item)">
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';
	import Dynamic_option_tables_state from '@/store/Dynamic_option_tables_state.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
			option_value(item)
			{
				for (let key in item) {
					if (! item.hasOwnProperty(key)) { continue; }
					// we have two types of enum options, one straight with key=>value, other also with useless category data key=>object
					if (typeof item[key] === 'string') {
						return key;
					} else if (typeof item[key] === 'object') {
						return item[key].value;
					}
				}
			},
			option_label(item)
			{
				for (let key in item) {
					if (! item.hasOwnProperty(key)) { continue; }
					// we have two types of enum options, one straight with key=>value, other also with useless category data key=>object
					if (typeof item[key] === 'string') {
						return item[key];
					} else if (typeof item[key] === 'object') {
						return item[key].label;
					}
				}
			},
			get_label_by_value(event_value)
			{
				// method used for auto_model select type only
				let labels = {};
				for (let order in this.field_data.enum) {
					if (! this.field_data.enum.hasOwnProperty(order)) { continue; }
					let key = Object.keys(this.field_data.enum[order])[0];
					let label = this.field_data.enum[order][key];
					// we have two types of enum options, one straight with key=>value, other also with useless category data key=>object
					if (typeof label === 'object') {
						label = label.label;
					}
					labels[key] = label;
				}
				return labels[event_value];
			},
		},
		computed: {
			options_table()
			{
				// SPECIAL CAR INPUTS ONLY
				let stored = Dynamic_option_tables_state().get_table_options[this.field_id];
				if (stored !== undefined) {
					return stored;
				}
				return this.field_data.enum;
			},
			select_2_class_style()
			{
				// does nothing, for now, not implemented yet
				/*if ( field_data.type_settings.select_type === 'simple' ) {
					content = content.replace( '{{select_2_class}}', '' );
					content = content.replace( '{{select_2_style}}', '' );
				} else if ( field_data.type_settings.select_type === 'searchable' ) {
					content = content.replace( '{{select_2_class}}', 'select2_initialize select2' );
					content = content.replace( '{{select_2_style}}', 'style="width:100%"' );
				} else if ( field_data.type_settings.select_type === 'large_ochorenie' ) {
					content = content.replace( '{{select_2_class}}', 'large_select' );
					content = content.replace( '{{select_2_style}}', '' );
				} else {
					// console.log( 'undefined select_type for: ' + field_id );
					content = content.replace( '{{select_2_class}}', '' );
					content = content.replace( '{{select_2_style}}', '' );
				}*/
			},
			disabled()
			{
				if (this.field_data.disabled === 'true') {
					return true;
				}
				return false;
			},
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, '');
				},
				set(event_value) {
					State.save_user_input(this.field_id, event_value, this.get_label_by_value(event_value));
				},
			},
		},
	}
</script>
