<template>
	<div class="row">
		<div
			v-for="index in tab_last"
			class="col text-center"
		>
			<span
				@click="change_tab(index)"
				style="padding: 6px 12px;"
				:style="style_based_on_position(index)"
			>
				{{ index }}
			</span>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			tab_current: Number,
			tab_last: Number,
		},
		components: {
		},
		emits: ['next', 'tab_change'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			change_tab(index)
			{
				if (index === this.tab_current + 1) {
					this.$emit('next');
				}
				if (index >= this.tab_current) {
					return;
				}
				this.$emit('tab_change', index);
			},
			style_based_on_position(index)
			{
				if (index < this.tab_current) {
					return {
						'background-color': '#C1CDDE',
						cursor: 'pointer',
					};
				} else if (index === this.tab_current) {
					return {
						'background-color': '#8D8F99',
						border: '1px solid black',
						cursor: 'not-allowed',
					};
				} else if (index === this.tab_current + 1) {
					return {
						'background-color': '#C1CDDE',
						cursor: 'pointer',
					};
				} else /* index > this.tab_current + 1 */ {
					return {
						'background-color': '#C1CDDE',
						cursor: 'not-allowed',
					};
				}
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
