import { createApp } from 'vue'
import App from './App.vue'
import store from './store'
import State from './store/State.js'
import { createPinia } from 'pinia'
const pinia = createPinia()

class Osm_app
{
	app = {};

	/**
	 * init
	*/
	init(config)
	{
		this.app = createApp(App).use(store).use(pinia);
		this.app.provide('config', config).mount('#osm_app');
	}

	/**
	 * destroy
	*/
	destroy()
	{
		this.app.unmount();
		this.app = '';
	}

	/**
	 * dump
	*/
	dump()
	{
		return {
			vuex: structuredClone(store.state),
			pinia: structuredClone(pinia.state.value),
		};
	}

	/**
	 * insert_new_bearer
	*/
	insert_access_token(string)
	{
		State.save_bearer(string);
	}

	/**
	 * autofill
	*/
	autofill_run()
	{
		State.autofill_run();
	}
}

window.osm_app = new Osm_app;
