import { defineStore } from 'pinia';

export default defineStore(
	'Dynamic_option_tables_state',
	{
		state: () => {
			return {
				tables: [],
			};
		},
		getters: {
			get_table_options()
			{
				return this.tables;
			},
		},
		actions: {
			set_table_options(table, options)
			{
				this.tables[table] = options;
			},
		},
	}
);
