<template>
	<textarea class="form-control" :id="field_id" :name="field_id" :placeholder="placeholder" v-model="value" :rows="area_rows"></textarea>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
		},
		computed: {
			placeholder()
			{
				return this.field_data.placeholder;
			},
			area_rows()
			{
				return 3;
				/*if ( field_data.wrapper_attrs === '' ) {
					content = content.replace( '{{area_rows}}', 'rows="3"' ); // if attributes are not sent, set default rows
				} else {
					content = content.replace( '{{area_rows}}', field_data.wrapper_attrs );
				}*/
			},
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, '');
				},
				set(event_value) {
					State.save_user_input(this.field_id, event_value);
				},
			},
		},
	}
</script>
