<template>
	<div class="input-group input-counter">
		<div class="input-group-prepend">
			<a class="input-group-btn btn-minus" @click="substract()"></a>
		</div>
		<input type="text" class="form-control" :id="field_id" :name="field_id" v-model="value" maxlength="3">
		<div class="input-group-append">
			<a class="input-group-btn btn-plus" @click="add()"></a>
		</div>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
			add()
			{
				this.value++;
				State.save_user_input(this.field_id, this.value);
			},
			substract()
			{
				this.value--;
				State.save_user_input(this.field_id, this.value);
			},
		},
		computed: {
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, 0);
				},
				set(event_value) {
					State.save_user_input(this.field_id, event_value);
				},
			},
		},
	}
</script>
