<template>
	<input type="text" class="form-control" :id="field_id" :name="field_id" :placeholder="placeholder" v-model="value" :disabled="disabled">
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
		},
		computed: {
			placeholder()
			{
				return this.field_data.placeholder;
			},
			html_validation()
			{
				// TODO html validation via attributes, f.e. maxlength="10"
				if (this.field_data.attributes === undefined) {
					return '';
				} else {
					var attributes = '';
					for (var key in this.field_data.attributes) {
						attributes += key + '="' + this.field_data.attributes[key] + '" ';
					}
					return attributes;
				}
			},
			disabled()
			{
				if (
					this.field_data.type_settings !== null &&
					this.field_data.type_settings !== undefined &&
					this.field_data.type_settings.disabled === 'true'
				) {
					return true;
				}
				return false;
			},
			value:
			{
				get() {
					return Functions.value_from_storage_or_session(this.field_id, this.field_data, '');
				},
				set(event_value) {
					State.save_user_input(this.field_id, event_value);
				},
			},
		},
	}
</script>
